.page {
  font-family: Inter, sans-serif;
  color: #1b2129;
  font-size: 16px;
  line-height: 140%;

  scroll-behavior: smooth;
  &__body {
    margin: 0;
  }

  &__menu menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    opacity: 0;
    pointer-events: none;

    &:target {
        opacity: 1;
        pointer-events: all;
      }
  }

}

